import Framebuffer from "./Framebuffers/Framebuffer";
import Texture from "../AssetHandling/Textures/Texture";
import Camera from "../Objects/Camera";
import Scene from "./Scene";
import ScreenQuadPass from "./RenderPasses/ScreenQuadRenderPass";
import { createScreenQuadShaderProgram } from "./ShaderPrograms/ScreenQuadShaderProgram";
import SkyboxRenderPass from "./RenderPasses/SkyboxRenderPass";
import GeometryRenderPass from "./RenderPasses/DefferedRendering/GeometryRenderPass";
import LightingRenderPass from "./RenderPasses/DefferedRendering/LightingRenderPass";
import DirectionalShadowRenderPass from "./RenderPasses/ShadowPasses/DirectionalShadowRenderPass";
import PointShadowRenderPass from "./RenderPasses/ShadowPasses/PointShadowRenderPass";
import { createLightingPassShaderProgram } from "./ShaderPrograms/DeferredRendering/LightingPassShaderProgram";
import { createGeometryPassShaderProgram } from "./ShaderPrograms/DeferredRendering/GeometryPassShaderProgram";
import { createDirectionalShadowShaderProgram } from "./ShaderPrograms/ShadowMapping/DirectionalShadowShaderProgram";
import { createPointShadowShaderProgram } from "./ShaderPrograms/ShadowMapping/PointShadowShaderProgram";
import { createSkyboxShaderProgram } from "./ShaderPrograms/Skybox/SkyboxShaderProgram";
import { textureStore } from "../../../Engine";
import { createVolumetricLightingShaderProgram } from "./ShaderPrograms/Volumetric/VolumetricLightingShaderProgram";
import VolumetricLightingPass from "./RenderPasses/Volumetric/VolumetricLightingPass";
import ParticleRenderPass from "./RenderPasses/Particles/ParticleRenderPass";
import { createParticleShaderProgram } from "./ShaderPrograms/Particles/ParticleShaderProgram";

export let gl: WebGL2RenderingContext;
export let rendererStartTime: number = Date.now();

export default class Renderer {
	// public
	clearColour: { r: number; g: number; b: number; a: number };

    domElement: HTMLCanvasElement;

	private width: number;
	private height: number;

	// ---- Shadows ----
	private directionalShadowRenderPass: DirectionalShadowRenderPass;
	private pointShadowRenderPass: PointShadowRenderPass;
	// -----------------

	// ---- Deferred rendering ----
	private geometryRenderPass: GeometryRenderPass;
	private lightingRenderPass: LightingRenderPass;
	// ----------------------------

	// ---- Skybox ----
	private useSkybox: boolean;
	private skyboxRenderPass: SkyboxRenderPass;
	// ----------------

	// ---- Particles ----
	private particleRenderPass: ParticleRenderPass;
	// -------------------

	// ---- Volumetric God Rays ----
	private volumetricLightingPass: VolumetricLightingPass;
	useVolumetric: boolean;
	// -----------------------------

	// ---- Finished output ----
	private finishedFramebuffer: Framebuffer;
	private finishedOutputRenderPass: ScreenQuadPass;
	// -------------------------

	constructor() {
		this.initGL();
		this.createShaders();
		this.width = 100;
		this.height = 100;

		// ---- Shadows ----
		this.directionalShadowRenderPass = new DirectionalShadowRenderPass();
		this.pointShadowRenderPass = new PointShadowRenderPass();
		// -----------------

		// ---- Deferred rendering ----
		this.geometryRenderPass = new GeometryRenderPass();

		let textureArray = new Array<Texture>();
		for (let i = 0; i < this.geometryRenderPass.outputFramebuffer.textures.length; i++) {
			textureArray.push(this.geometryRenderPass.outputFramebuffer.textures[i]);
		}
		textureArray.push(this.directionalShadowRenderPass.shadowBuffer.depthTexture);
		this.lightingRenderPass = new LightingRenderPass(textureArray);
		// ----------------------------

		// ---- Skybox ----
		this.useSkybox = false;
		this.skyboxRenderPass = new SkyboxRenderPass();
		// ----------------

		// ---- Paricles ----
		this.particleRenderPass = new ParticleRenderPass();
		// ------------------

		// ---- Volumetric God Rays ----
		this.volumetricLightingPass = new VolumetricLightingPass(this.geometryRenderPass.outputFramebuffer.textures[0], this.directionalShadowRenderPass.shadowBuffer.depthTexture);
		this.useVolumetric = false;
		// -----------------------------

		this.finishedFramebuffer = new Framebuffer(
			this.width,
			this.height,
			[new Texture(false)],
			null
		);

		// Assign finished framebuffer to those render passes that should target it
		this.volumetricLightingPass.outputBuffer = this.finishedFramebuffer;
		this.particleRenderPass.outputBuffer = this.finishedFramebuffer;

		this.finishedOutputRenderPass = new ScreenQuadPass(this.finishedFramebuffer.textures[0]);
	}

	private createShaders() {
		createGeometryPassShaderProgram();
		createLightingPassShaderProgram();
		createDirectionalShadowShaderProgram();
		createPointShadowShaderProgram();
		createParticleShaderProgram();
		createVolumetricLightingShaderProgram();
		createSkyboxShaderProgram();
		createScreenQuadShaderProgram();
	}

	initGL() {
	    this.domElement = <HTMLCanvasElement>document.createElement("canvas");

        gl = this.domElement.getContext("webgl2", { antialias: false });
        if (!gl.getExtension("EXT_color_buffer_float")) {
            alert("Rendering to floating point textures is not supported on this platform");
        }
        if (!gl.getExtension("OES_texture_float_linear")) {
            alert("Floating point rendering to FBO textures not supported");
        }

        if (!gl) {
            console.log("Failed to get rendering context for WebGL");
            return;
        }

		this.clearColour = { r: 0.0, g: 0.0, b: 0.0, a: 1.0 };
		gl.clearColor(this.clearColour.r, this.clearColour.g, this.clearColour.b, this.clearColour.a);

		// Enable depth test
		gl.enable(gl.DEPTH_TEST);

		// Disable alpha blending since we are using screen door transparancy
		gl.disable(gl.BLEND);

		// Enable back face culling
		gl.cullFace(gl.BACK);
		gl.enable(gl.CULL_FACE);
	}

	setSize(x: number, y: number, updateStyle: boolean = false) {
		this.width = x;
		this.height = y;

		this.domElement.width = this.width;
		this.domElement.height = this.height;

		this.geometryRenderPass.setResolution(x, y);
		this.finishedFramebuffer.setProportions(x, y);
	}

	setSkybox(path?: string) {
		if (path != undefined) {
			this.skyboxRenderPass.setSkybox(textureStore.getCubeMap(path));
			// this.skyboxRenderPass.setSkybox(this.scene.pointLights[0].pointShadowDepthMap);
			this.useSkybox = true;
		}
		else {
			this.useSkybox = false;
		}
	}

	setFogDensity(density: number) {
		this.volumetricLightingPass.fogDensity = density;
	}

	private takeScreenshot(screenshotName: string) {
		var offscreenCanvas = document.createElement("canvas");
		offscreenCanvas.width = gl.canvas.width;
		offscreenCanvas.height = gl.canvas.height;
		var ctx = offscreenCanvas.getContext("2d");

		ctx.drawImage(gl.canvas, 0, 0);

		const saveBlob = (function () {
			const a = document.createElement("a");
			document.body.appendChild(a);
			a.style.display = "none";
			return function saveData(blob, fileName) {
				const url = window.URL.createObjectURL(blob);
				a.href = url;
				a.download = fileName;
				a.click();
			};
		})();

		offscreenCanvas.toBlob((blob) => {
			saveBlob(blob, screenshotName);
		});
	}

	render(scene: Scene, camera: Camera, saveScreenshot: boolean = false, screenshotName: string = "screencapture") {
		gl.enable(gl.DEPTH_TEST);

		scene.calculateAllTransforms();

		// ---- Shadow pass ----
		this.directionalShadowRenderPass.draw(scene);
		this.pointShadowRenderPass.draw(scene);
		// ---------------------

		// ---- Geometry pass ----
		this.geometryRenderPass.draw(scene, camera);
		// -----------------------

		// Geometry pass over, start rendering to particle render pass output
		this.particleRenderPass.bindFramebuffers();

		// Clear the output with the actual clear colour we have set
		gl.clearColor(this.clearColour.r, this.clearColour.g, this.clearColour.b, this.clearColour.a);
		gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT | gl.STENCIL_BUFFER_BIT);

		// ---- Lighting pass ----
		this.lightingRenderPass.draw(scene, camera);
		// -----------------------

		// Copy the depth buffer information from the gBuffer to the current depth buffer
		this.geometryRenderPass.outputFramebuffer.bind(gl.READ_FRAMEBUFFER);
		gl.blitFramebuffer(
			0,
			0,
			this.width,
			this.height,
			0,
			0,
			this.width,
			this.height,
			gl.DEPTH_BUFFER_BIT,
			gl.NEAREST
		);

		// ---- Skybox ----
		if (this.useSkybox) {
			this.skyboxRenderPass.draw(camera);
		}
		// ----------------

		// ---- Particles ----
		this.particleRenderPass.draw(scene, camera);
		// -------------------

		// ---- Volumetric God Rays ----
		if (this.useVolumetric) {
			this.volumetricLightingPass.draw(scene, camera);
		}
		// -----------------------------

		this.finishedOutputRenderPass.draw();

        if (saveScreenshot) {
            this.takeScreenshot(screenshotName);
        }
	}
}
